<template>
    <v-container fluid fill-height style="padding:0;" id="proxy" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Proxy">Proxy</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12>
                                            <div class="part">
                                                <p>1. <code>Proxy</code> 可以帮助对象拦截一些操作，从而在操作完成前做一些自定义的设置</p>
                                                <p>2. <code>Proxy</code> 的重心在于拦截操作从而做一些其他的处理，而不是修改要拦截方法本来的操作和返回值</p>
                                                <p>3. <code>Proxy</code> 能够拦截的所有的handler如下</p>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12>
                                        <v-select
                                            :items="handlerArrs"
                                            label="代理方法"
                                            style="width: 200px;"
                                            color="#7cb242"
                                            v-model="handlerValue"
                                        ></v-select>
                                        </v-flex>
                                        <v-flex xs12>
                                            <v-card flat>
                                                <v-card-text>
                                                    <pre class="code">{{pres[handlerValue]}}</pre>
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>

                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Reflect">Reflect</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12>
                                            <div class="part">
                                                <p><code>Reflect</code> 是 <code>Proxy</code>的静态版本，不需要使用new创建实例而已</p>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12>
                                        <v-select
                                            :items="handlerArrs"
                                            label="代理方法"
                                            style="width: 200px;"
                                            color="#7cb242"
                                            v-model="handlerValue1"
                                        ></v-select>
                                        </v-flex>
                                        <v-flex xs12>
                                            <v-card flat>
                                                <v-card-text>
                                                    <pre class="code">{{pres1[handlerValue1]}}</pre>
                                                </v-card-text>
                                            </v-card>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from "timers";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-12-26",
            title: "Proxy代理操作",
            items: [
                {
                    title: "Proxy",
                    target: "#Proxy"
                },
                { divider: true },
                {
                    title: "Reflect",
                    target: "#Reflect"
                }
            ],
            handlerArrs: [
                'apply', 
                'construct', 
                'defineProperty',
                'deleteProperty',
                'get',
                'getOwnPropertyDescriptor',
                'getPrototypeOf',
                'has',
                'isExtensible',
                'ownKeys',
                'preventExtensions',
                'set',
                'setPrototypeOf'],
            handlerValue: 'apply',
            handlerValue1: 'apply',
            pres: 
{
apply: `
    // 代理：函数调用
    function fn(a, b) {
        return a + b;
    }

    const px = new Proxy(fn, {
        apply: function(fn, that, params) {
            // fn表示原始的函数
            // that表示传递的this对象
            // params表示传递的参数列表
            return fn(...params);
        }
    });

    // 直接调用px(1,2)，px.apply，px.call都可以被拦截
    px.call({}, 1, 2);
`,
construct: `
    // 代理：new操作符
    const px = new Proxy(Date, {
        construct: function(date, params, newdate) {
            // date表示原始的构造函数
            // params表示传递的参数列表
            // newdate === px
            return new date(...params);
        }
    });

    console.log(new px('2019-05-20T24:00:00'));
`,
defineProperty: `   
    // 代理：对象的defineProperty，定义对象属性也会触发
    let obj = {};
    const px = new Proxy(obj, {
        defineProperty(obj, key, descriptor) {
            // obj表示原始的对象
            // key表示设置的键名
            // descriptor表示属性描述对象
            Object.defineProperty(obj, key, descriptor);
            return true; // 返回true表示属性定义成功
        }
    });
    px.a = 'aa';
`,
deleteProperty: `
    // 代理：delete操作符
    let obj = {
        a: 'aa'
    };
    const px = new Proxy(obj, {
        deleteProperty: function(obj, key) {
            // obj表示原始的对象
            // key表示设置的键名
            delete obj[key];
            return true; // true表示删除成功
        }
    });
    delete px.a;
`,
get: `
    // 代理：get
    let obj = {
        a: 'aa'
    };

    const px = new Proxy(obj, {
        get: function(obj, key, receiver) {
            console.log(obj, key, receiver === px);
            return obj.px;
        }
    });

    console.log(px.a);
`,
getOwnPropertyDescriptor: `
    // 代理：getOwnPropertyDescriptor
    let obj = {
        a: 'aa'
    };

    const px = new Proxy(obj, {
        getOwnPropertyDescriptor(obj, key) {
            return { writable: true, configurable: true, enumerable: true, value: obj[key] };
        }
    });

    console.log(Object.getOwnPropertyDescriptor(px, 'a'));
`,
getPrototypeOf: `
    // 代理：getPrototypeOf
    const parent = {};
    let obj = Object.create(parent);

    const px = new Proxy(obj, {
        getPrototypeOf(obj) {
            console.log(obj);
            return parent;
        }
    });

    parent === Object.getPrototypeOf(px);
`,
has: `
    // 代理：in 判断属性是否在对象中
    const obj = {
        a: 'aa'
    };

    const px = new Proxy(obj, {
        has (obj, key) {
            console.log(obj, key);
            return key in obj;
        }
    });

    console.log('a' in px);
`,
isExtensible: `
    // 代理：isExtensible
    const obj = {
        a: 'aa'
    };

    const px = new Proxy(obj, {
        isExtensible (obj) {
            console.log(obj);
            return Object.isExtensible(obj);
        }
    });

    console.log(Object.isExtensible(px));
`,
ownKeys: `
    // 代理：获取对象键名
    const obj = {
        [Symbol('a')]: 'aa'
    };

    const px = new Proxy(obj, {
        ownKeys (obj) {
            console.log(obj);
            return Reflect.ownKeys(obj);
        }
    });

    console.log(Reflect.ownKeys(px));
`,
preventExtensions: `
    // 代理：preventExtensions
    const obj = {
        a: 'aa'
    };

    const px = new Proxy(obj, {
        preventExtensions (obj) {
            console.log(obj);
            Object.preventExtensions(obj);
            return true;
        }
    });

    console.log(Object.preventExtensions(px));
`,
set: `
    // 代理：set 设置对象属性值
    const obj = {
        a: 'aa'
    };

    const px = new Proxy(obj, {
        set (obj, key, value, receiver) {
            console.log(obj, key, value, receiver);
            obj[key]=value;
            return true;
        }
    });

    px.a = 'aaa';
`,
setPrototypeOf: `
    // 代理：setPrototypeOf
    const obj = {};

    const px = new Proxy(obj, {
        setPrototypeOf (obj, prototype) {
            console.log(obj, prototype);
            Object.setPrototypeOf(obj, prototype);
            return true;
        }
    });

    Object.setPrototypeOf(px, {a: 'aa'});
    console.log(obj.a);
`
},

pres1: 
{
apply: `
    // 代理：函数调用
    function fn(a, b) {
        return a + b;
    }
    Reflect.apply(fn, {}, [1, 2]);
`,
construct: `
    // 代理：new操作符
    Reflect.construct(Date, ['2019-05-20T24:00:00']);
`,
defineProperty: `   
    // 代理：对象的defineProperty，定义对象属性也会触发
    let obj = {};
    Reflect.defineProperty(obj, 'a', {value: 42});
`,
deleteProperty: `
    // 代理：delete操作符
    let obj = {
        a: 'aa'
    };

    Reflect.deleteProperty(obj, 'a');
`,
get: `
    // 代理：get
    let obj = {
        a: 'aa'
    };

    Reflect.get(obj, 'a');
`,
getOwnPropertyDescriptor: `
    // 代理：getOwnPropertyDescriptor
    let obj = {
        a: 'aa'
    };

    Reflect.getOwnPropertyDescriptor(obj, 'a');
`,
getPrototypeOf: `
    // 代理：getPrototypeOf
    const parent = {};
    let obj = Object.create(parent);
    parent === Reflect.getPrototypeOf(obj);
`,
has: `
    // 代理：in 判断属性是否在对象中
    const obj = {
        a: 'aa'
    };

    Reflect.has(obj, 'a');
`,
isExtensible: `
    // 代理：isExtensible
    const obj = {
        a: 'aa'
    };

    Reflect.isExtensible(obj);
`,
ownKeys: `
    // 代理：获取对象键名
    const obj = {
        [Symbol('a')]: 'aa'
    };

    Reflect.ownKeys(obj);
`,
preventExtensions: `
    // 代理：preventExtensions
    const obj = {
        a: 'aa'
    };

    Reflect.preventExtensions(obj);
`,
set: `
    // 代理：set 设置对象属性值
    const obj = {
        a: 'aa'
    };

    Reflect.set(obj, 'a', 'aaa');
`,
setPrototypeOf: `
    // 代理：setPrototypeOf
    const obj = {};
    Reflect.setPrototypeOf(obj, {a: 'aa'});
    console.log(obj.a);
`
},
        }
    },
    mounted() {},
    methods: {}
};
</script>

